body{
    font-family: 'Montserrat', sans-serif;
    font-size: 15px;
    line-height: 30px;
    background-color: #fff;
}
h1,h2,h3,h4,h5,h6{
    font-weight: 800;
}
$color: #1e1e1e;
.sec-title{
    font-weight: 800;
    font-size: 58px;
}
.btn-primary {
    color: #1e1e1e;
    background-color: #ffe600;
    border-color: #ffe600;
    padding: 10px 30px;
    &:hover{
        background-color: #1e1e1e;
        color: #fff;
        border-color: #1e1e1e;
    }
}
.header{
    .navbar-light .navbar-nav .nav-link{
        font-size: 16px;
        font-weight: 500;
        padding: 15px 30px;
        text-transform: uppercase;
    }
}
.banner-section {
    padding: 150px 0;
    .banner-img{
        text-align: right;
        
    }
}
.banner-txt{
    h1{
        font-size: 60px;
        line-height: 80px;
        color: $color;
        font-weight: 800;
        span{
            display: block;
        }
    }
    p{
        max-width: 90%;
       margin: 30px 0;
    }
}

/*
    Portfolio CSS
*/
.portfolio-menu {
    margin: 60px 0;
    ul{
        padding: 0;
        margin: 0;
        list-style: none;
        li{
            display: inline-block;
            a{
                color: #1e1e1e;
                padding: 10px 20px;
                text-decoration: none;
                &.active{
                    background-color: #ffe600;
                }
            }
        }
    }
}
.portfolio-item{
    position: relative;
    margin-bottom: 30px;
    cursor: pointer;
    &::before{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        content: "";
        background-color: #ffe600;
    }
    img{
        width: 100%;
    }
    .pt-info {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
        opacity: 0;
        transition: .4s;
        z-index: 2;
        h3{
            font-size: 30px;
        }
    }
    &:hover .pt-info{
        opacity: 1;
    }
}
.grid-sizer, .portfolio-item {
    background-clip: content-box;
    width: 31.33%;
}